import { http } from '@/http';
import Toast from "vant/es/toast";
import "vant/es/toast/style/less";
import { computed, defineComponent } from 'vue';
import { useRouter } from 'vue-router';
import { useStore } from 'vuex';
export default defineComponent({
    name: 'FansBind',
    setup() {
        const store = useStore();
        const shopCode = computed(() => store.state.shopCode);
        const bindChannel = computed(() => store.state.bindChannel);
        const router = useRouter();
        const handleClickBind = () => {
            http
                .post('/hbSeller/fans/bindShopInvite', {
                shopCode: shopCode.value,
                bindChannel: bindChannel.value
            }, false)
                .then((res) => {
                if (res.code === '200') {
                    Toast.success('绑定成功!');
                    setTimeout(() => {
                        router.push('/customer/home');
                    }, 3000);
                }
                else if (res.code === '600') {
                    Toast.fail(res.msg);
                    setTimeout(() => {
                        router.push('/customer/home');
                    }, 3000);
                }
                else {
                    Toast.fail(res.msg);
                }
            });
        };
        return { handleClickBind };
    }
});
